body {
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

main {
  margin: 48px;
  margin-top: 0;
  background: #fff;
  border-radius: 4px;
  width: 50%;
}

header {
  line-height: 48px;
  padding: 0 48px;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  border-bottom: 1px solid #e5e5e5;
}

div {
  padding: 160px 40px;
  height: 600px;
}

p {
  text-align: center;
  font-size: 18px;
  font-family: monospace;
  word-break: break-all;
}

a {
  cursor: pointer;

  &:hover {
    color: #58e;
  }
}
